@font-face {
    font-family: 'Segoe UI';
    src: url('/surface/Assets/css/fonts/all/normal/segoeui.eot');
    src: url('/surface/Assets/css/fonts/all/normal/segoeui.eot?#iefix') format('embedded-opentype'),
         url('/surface/Assets/css/fonts/all/normal/segoeui.svg#SegoeUI') format('svg');
    font-weight: normal;
    font-style: normal;
}


body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-image: url("./assets/trumpBackground.jpg");
  background-position: bottom;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

a {
    text-decoration: none !important;
}

.tweetTimeline {
    color: rgb(20, 23, 26);
    font-family: "Segoe UI", Arial, sans-serif;
    font-size: 14px;
    line-height: 19.25px;
    text-align: left;
    display: block;
    position: relative;
    width: 100%;
    margin-left: auto;
    margin-right: 0;
}
@media (min-width: 1024px) {
    .tweetTimeline {
        margin-right: 10%;
        max-width: 590px;
    }
}

.tweetWrapper {
    background-color: rgb(255, 255, 255);
    border-left: 1px solid rgb(230, 236, 240);
    border-right: 1px solid rgb(230, 236, 240);
    display: block;
    position: relative;
    margin: 0;
}

    .tweetOuterContent {
        border-bottom: 1px solid rgb(230, 236, 240);
        cursor: pointer;
        display: block;
        min-height: 51px;
        padding: 9px 12px 9px 12px;
        position: relative;
    }
    .tweetOuterContent:hover {
        background-color: rgb(245, 248, 250);
    }

        .tweetContent {
            margin-left: 58px;
            display: block;
        }
            .tweetHeader {
                display: -ms-flexbox;
                display: flex;
            }
                .accountGroup {
                    cursor: auto;
                    display: -ms-flexbox;
                    display: flex;
                    -ms-flex-negative: 1;
                        flex-shrink: 1;
                    margin-right: 5px;
                    overflow: hidden;
                    text-decoration: none;
                    color: rgb(20, 23, 26);
                }
                .accountGroup:hover .accountName {
                    color: rgb(0, 172, 237);
                    text-decoration: underline;
                }
                .accountGroup:hover .accountUsername {
                    -webkit-text-decoration-line: none;
                            text-decoration-line: none;
                }
                    .accountPicture {
                        border-radius: 50%;
                        border: 0px solid rgb(20, 23, 26);
                        float: none;
                        height: 48px;
                        list-style-image: none;
                        list-style-position: outside;
                        list-style-type: none;
                        margin-left: -58px;
                        margin-top: 3px;
                        position: absolute;
                        width: 48px;
                    }
                    .accountName {
                        overflow: hidden;
                        -o-text-overflow: ellipsis;
                           text-overflow: ellipsis;
                        white-space: nowrap;
                        word-wrap: normal;
                        font-weight: 700;
                        word-break: break-all;
                        padding-right: 3px;
                    }
                    .accountUsername {
                        color: rgb(101, 119, 134);
                        text-decoration: none;
                    }
            .tweetText {
                white-space: pre-wrap;
                word-wrap: break-word;
                color: rgb(20, 23, 26);
                font-size: 14px;
                line-height: 20px;
                font-weight: 400;
                margin: 0;
            }
                .tweetLink {
                    text-decoration: none;
                    color: rgb(0, 172, 237);
                    font-weight: 400;
                }
                .tweetLink:hover {
                    -webkit-text-decoration-line: underline;
                            text-decoration-line: underline;
                }
                .grammarText {
                    display: inline;
                    position: relative;
                    background-color: rgba(255, 231, 76, 0.65);
                    border-top-right-radius: 2px;
                    border-top-left-radius: 2px;
                    border-bottom: 2px solid rgba(234, 212, 70, 1.0);
                    padding-left: 3px;
                    padding-right: 3px;
                }
                .styleText {
                    display: inline;
                    position: relative;
                    background-color: rgba(73, 189, 255, 0.5);
                    border-top-right-radius: 2px;
                    border-top-left-radius: 2px;
                    border-bottom: 2px solid rgba(73, 189, 255, 1.0);
                    padding-left: 3px;
                    padding-right: 3px;
                }
                .spellingText {
                    display: inline;
                    position: relative;
                    background-color: rgba(255, 89, 100, 0.5);
                    border-top-right-radius: 2px;
                    border-top-left-radius: 2px;
                    border-bottom: 2px solid rgba(255, 89, 100, 1.0);
                    padding-left: 3px;
                    padding-right: 3px;
                }
            .tweetFooter {
                display: block;
                height: 20px;
            }

/* Custom Popover Styles */
.popover-title {
    font-weight: 700;
}
    /* Grammar errors */
    #popoverGrammar {
        background-color: rgba(255, 240, 140, 1.0);
        border: 0px;
        padding: 0px;
    }
    #popoverGrammar .arrow:after {
        border-top-color: rgba(255, 240, 140, 1.0);
    }
    #popoverGrammar .arrow {
        border-top-color: rgba(255, 231, 76, 1.0);
    }
    #popoverGrammar .popover-title {
        background-color: rgba(255, 231, 76, 1.0);
        border: 0px;
    }

    /* Spelling errors */
    #popoverSpelling {
        background-color: rgba(255, 174, 178, 1.0);
        border: 0px;
        padding: 0px;
    }
    #popoverSpelling .arrow:after {
        border-top-color: rgba(255, 174, 178, 1.0);
    }
    #popoverSpelling .arrow {
        border-top-color: rgba(255, 143, 150, 1.0);
    }
    #popoverSpelling .popover-title {
        background-color: rgba(255, 143, 150, 1.0);
        border: 0px;
    }

    /* Style errors */
    #popoverStyle {
        background-color: rgba(165, 222, 255, 1.0);
        border: 0px;
        padding: 0px;
    }
    #popoverStyle .arrow:after {
        border-top-color: rgba(165, 222, 255, 1.0);
    }
    #popoverStyle .arrow {
        border-top-color: rgba(112, 203, 255, 1.0);
    }
    #popoverStyle .popover-title {
        background-color: rgba(112, 203, 255, 1.0);
        border-bottom: 0px;
    }



